@media screen and (min-width: 319px) and (max-width: 769px) {
  .header-menu-mob {
    stroke: #111111;
    transition: all 0.3s;
    width: 28px;
    height: 28px;
    background-color: #ffffff;
  }
  .header-menu-mob:hover {
    stroke: #4f2ee8;
  }
  .header-menu-mob:focus {
    stroke: #4f2ee8;
  }
  .menu-burger-open {
    cursor: pointer;
    background-color: #111111;
    border: none;
    border-radius: 50%;
    outline: none;
    justify-content: center;
    align-items: flex-end;
    margin-left: 14px;
    padding: 0;
    transition: all 0.3s ease;
    display: flex;
  }
  .menu-burger-open .header-menu-mob {
    opacity: 1;
    transition: opacity 0.3s ease;
  }
  .menu-burger-open .header-menu-mob-close {
    transition: opacity 0.3s ease;
    background-color: #ffffff;
  }
  .mobile-menu-container {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    width: 100%;
    height: 90vh;
    z-index: 99;
    background: url("../images/png/background-mobile-burger.png"), radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #ffffff 100%);
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: contain;
    flex-direction: column;
    transition: all 0.3s;
    display: flex;
    position: fixed;
    top: 58px;
    left: 0;
    transform: translateX(100%);
  }
  .mobile-menu-container.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: visible;
    transform: translate(0);
  }
  .mobile-menu-close-button {
    cursor: pointer;
    background-color: #111111;
    border: none;
    border-radius: 50%;
    outline: none;
    justify-content: center;
    align-items: flex-end;
    margin-left: auto;
    padding: 0;
    animation: all 0.3s;
    display: flex;
    width: 24px;
    height: 24px;
    align-items: center;
    margin: 18px;
  }
  .header-menu-mob-close.burger-toggle-hidden {
    display: none;
    transform: scale(0);
    transition: opacity 0.3s ease-out, transform 0.3s ease-out;
  }
  .header-menu-mob.burger-toggle-hidden {
    display: none;
    transition: opacity 0.3s ease-out, height 0.3s ease-out;
  }
  .header-menu-mob-close {
    stroke: #111111;
    transition: all 0.3s;
    display: flex;
  }
  .header-menu-mob-close:hover {
    stroke: #4f2ee8;
  }
  .header-menu-mob-close:focus {
    stroke: #4f2ee8;
  }
  .user-info-bar {
    height: 37px;
    align-items: center;
    gap: 9px;
    margin-top: 20px;
    margin-left: 20px;
    display: inline-flex;
  }
  .user-info-bar > .user-name {
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 1.31;
    color: #ffffff;
  }
  .nav-mob-menu,
.nav-list-mob-menu {
    margin: 0 auto;
    display: flex;
  }
  .nav .nav-active {
    background-color: #eac645;
    border-radius: 36px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    transition: all 0.3s;
    display: flex;
  }
  .nav-link {
    justify-content: center;
    align-items: center;
    gap: 4px;
    display: flex;
    color: #ffffff;
    text-transform: uppercase;
  }
  .nav-list {
    flex-direction: column;
    align-items: start;
    margin-top: 15.5vh;
    display: flex;
    justify-content: center;
    gap: 8px;
  }
  .nav-list .nav-active {
    background-color: #eac645;
    border-radius: 36px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
    transition: all 0.3s;
    display: flex;
  }
  .nav-item {
    z-index: 10;
    padding-top: 16px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.28;
    transition: all 0.3s;
    position: relative;
  }
  .btn-header-mob-logout {
    max-width: fit-content;
    text-transform: none;
    letter-spacing: -0.04em;
    color: #111111;
    background: #ffffff;
    border: 1.5px solid #111111;
    border-radius: 18px;
    gap: 4px;
    padding: 13px 17px;
    font-size: 14px;
    line-height: 1.28;
    transition: all 0.3s;
    display: flex;
    position: fixed;
    left: 20px;
    bottom: 20px;
  }
  .btn-header-mob-logout:hover,
.btn-header-mob-logout:focus {
    color: #ffffff;
    background: #4f2ee8;
  }
  .mobile-inactive-link {
    justify-content: center;
    align-items: center;
    gap: 4px;
    display: flex;
    color: #ffffff;
    text-transform: uppercase;
  }
}
@media screen and (min-width: 769px) and (max-width: 1440px) {
