.checkbox {
  opacity: 0;
  position: absolute;
}

.label-mode {
  background: linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
  border-radius: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  position: relative;
  height: 13px;
  width: 26.7px;
  transform: scale(1.5);
}

.label-mode .ball {
  background: linear-gradient(180deg, #ffffff 0%, #f6f6f6 100%);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), 0px 2px 2px rgba(0, 0, 0, 0.1), 1px 2px 4px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  position: absolute;
  height: 12px;
  width: 12px;
  transform: translateX(0px);
  transition: transform 400ms ease;
  top: 0;
  left: 0;
}

.checkbox:checked + .label-mode .ball {
  transform: translateX(14px);
}

.checkbox:checked + .label-mode {
  background: linear-gradient(180deg, #4f2ee8 0%, #686868 100%);
}

